
/* Navbar */
.navbar{
    position: sticky;
    top: 0;
    z-index: 1;
}

.navbar-brand{
    font-size: 45px;
}
.navbar-brand:hover{
    transform: scale(1.1);
    transition: all 0.5s ease;
}
.navbar-nav{
    font-size: 30px;
}
.nav-item:hover{
    transform: scale(1.2);
    transition: all 0.5s ease;
}

/* Button */
.btn:hover{
    color:white
}

/* Progress Bar */
.progress{
    height: 30px;
    border-radius: 50px;
    transition: all 0.5s ease;
}

.progress:hover {
    transform: scale(1.1)
}

/* Service Cards */
.card{
    transition: all 0.5s ease;
}

.card img{
    height: 200px;
    Width: 200px;
    Margin: auto;
}
.card:hover{
    transform: scale(1.1);
    box-shadow: 1px 2px 10px black;
}

.project-title{
    font-size: 45px;
}

.follow-me {
    font-size: 35px;
}

